import { Link } from "react-router-dom";

import { Container } from "../UI";

import "./Navbar.scss";

export const Navbar: React.FC = () => {
  return (
    <nav className="navbar__main">
      <Container>
        <div className="links__wrapper">
          <Link to="/"></Link>
          <Link to="/about"> </Link>
        </div>
      </Container>
    </nav>
  );
};
